import React, { useEffect, useState } from 'react';
import { Card, Spinner } from 'react-bootstrap';

const API_KEY = '74f4c65fa40120881e93ed2e2e832c7d';
const CITY = 'Leicester';

export default function Weather() {
  const [weather, setWeather] = useState(null);

  useEffect(() => {
    fetch(`https://api.openweathermap.org/data/2.5/weather?q=${CITY}&units=metric&appid=${API_KEY}`)
      .then(res => res.json())
      .then(data => {
        setWeather({
          temp: data.main.temp,
          condition: data.weather[0].main,
          icon: data.weather[0].icon
        });
      })
      .catch(console.error);
  }, []);

  if (!weather) {
    return <Spinner animation="border" role="status" size="sm">
      <span className="visually-hidden">Loading weather...</span>
    </Spinner>;
  }

  return (
    <Card border="info" style={{ maxWidth: '300px' }}>
      <Card.Body className="d-flex align-items-center">
        <img
          src={`https://openweathermap.org/img/wn/${weather.icon}@2x.png`}
          alt={weather.condition}
          className="me-3"
        />
        <div>
          <Card.Title>{CITY}</Card.Title>
          <Card.Text>
            {weather.temp}¬∞C ‚Äì {weather.condition}
          </Card.Text>
        </div>
      </Card.Body>
    </Card>
  );
}
