import React, { useState, useEffect, useCallback } from 'react';
import { Card, Button, Spinner } from 'react-bootstrap';

const CLIENT_ID = 'fa95a427198e4d4790c0be1b9a5d994c';
const REDIRECT_URI = 'https://j4mest.github.io/';
const SCOPES = 'user-read-currently-playing user-read-playback-state';
const AUTH_ENDPOINT = 'https://accounts.spotify.com/authorize';
const TOKEN_ENDPOINT = 'https://accounts.spotify.com/api/token';

function generateCodeVerifier() {
  const array = new Uint8Array(64);
  window.crypto.getRandomValues(array);
  return btoa(String.fromCharCode(...array))
    .replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
}

async function generateCodeChallenge(verifier) {
  const encoder = new TextEncoder();
  const data = encoder.encode(verifier);
  const digest = await window.crypto.subtle.digest('SHA-256', data);
  return btoa(String.fromCharCode(...new Uint8Array(digest)))
    .replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
}

export default function NowPlaying() {
  const [token, setToken] = useState(null);
  const [refreshToken, setRefreshToken] = useState(null);
  const [track, setTrack] = useState(null);

  // On mount: exchange code or load saved tokens
  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const code = params.get('code');
    const savedAccess = localStorage.getItem('access_token');
    const savedRefresh = localStorage.getItem('refresh_token');

    if (code) {
      const verifier = localStorage.getItem('pkce_verifier');
      fetch(TOKEN_ENDPOINT, {
        method: 'POST',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: new URLSearchParams({
          grant_type: 'authorization_code',
          code,
          redirect_uri: REDIRECT_URI,
          client_id: CLIENT_ID,
          code_verifier: verifier
        })
      })
        .then(res => res.json())
        .then(data => {
          localStorage.setItem('access_token', data.access_token);
          localStorage.setItem('refresh_token', data.refresh_token);
          setToken(data.access_token);
          setRefreshToken(data.refresh_token);
          // clear code from URL
          window.history.replaceState({}, document.title, window.location.pathname);
        })
        .catch(console.error);
    } else if (savedAccess) {
      setToken(savedAccess);
      setRefreshToken(savedRefresh);
    }
  }, []);

  const handleLogin = useCallback(async () => {
    const verifier = generateCodeVerifier();
    const challenge = await generateCodeChallenge(verifier);
    localStorage.setItem('pkce_verifier', verifier);

    window.location = `${AUTH_ENDPOINT}?client_id=${CLIENT_ID}` +
      `&response_type=code` +
      `&redirect_uri=${encodeURIComponent(REDIRECT_URI)}` +
      `&scope=${encodeURIComponent(SCOPES)}` +
      `&code_challenge_method=S256` +
      `&code_challenge=${challenge}`;
  }, []);

  // Refresh token helper
  const refreshAccessToken = useCallback(() => {
    if (!refreshToken) return;
    fetch(TOKEN_ENDPOINT, {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: new URLSearchParams({
        grant_type: 'refresh_token',
        refresh_token: refreshToken,
        client_id: CLIENT_ID
      })
    })
      .then(res => res.json())
      .then(data => {
        localStorage.setItem('access_token', data.access_token);
        setToken(data.access_token);
      })
      .catch(console.error);
  }, [refreshToken]);

  // Poll current track
  useEffect(() => {
    if (!token) return;
    const fetchNow = async () => {
      const res = await fetch('https://api.spotify.com/v1/me/player/currently-playing', {
        headers: { Authorization: `Bearer ${token}` }
      });
      if (res.status === 401) return refreshAccessToken();
      if (res.status === 204 || !res.ok) return setTrack(null);
      const data = await res.json();
      if (data.is_playing && data.item) {
        setTrack({
          title: data.item.name,
          artists: data.item.artists.map(a => a.name).join(', '),
          albumArt: data.item.album.images[0].url,
          url: data.item.external_urls.spotify
        });
      } else {
        setTrack(null);
      }
    };

    fetchNow();
    const interval = setInterval(fetchNow, 15000);
    return () => clearInterval(interval);
  }, [token, refreshAccessToken]);

  if (!token) {
    return <Button variant="success" onClick={handleLogin}>Log in with Spotify</Button>;
  }

  if (!track) {
    return <Spinner animation="border" role="status" size="sm">
      <span className="visually-hidden">Nothing playing right now.</span>
    </Spinner>;
  }

  return (
    <Card bg="dark" text="white" style={{ maxWidth: '400px' }}>
      <Card.Body className="d-flex">
        <img
          src={track.albumArt}
          alt={track.title}
          className="rounded me-3"
          style={{ width: '64px', height: '64px' }}
        />
        <div>
          <Card.Title>
            <a
              href={track.url}
              target="_blank"
              rel="noopener noreferrer"
              className="link-success"
            >
              üéß {track.title}
            </a>
          </Card.Title>
          <Card.Text>{track.artists}</Card.Text>
        </div>
      </Card.Body>
    </Card>
  );
}
