import React, { useState, useEffect } from 'react';
import { Card, ListGroup, Spinner } from 'react-bootstrap';

export default function CalendarEmbed() {
  const [events, setEvents] = useState(null);
  const dataUrl = '/calendar.json';

  useEffect(() => {
    fetch(dataUrl)
      .then(res => {
        if (!res.ok) throw new Error('Failed to load calendar JSON');
        return res.json();
      })
      .then(data => {
        const now = new Date();
        const upcoming = data
          .filter(evt => new Date(evt.end) >= now)
          .sort((a, b) => new Date(a.start) - new Date(b.start));
        setEvents(upcoming);
      })
      .catch(err => {
        console.error('Failed to load calendar', err);
        setEvents([]);
      });
  }, []);

  if (events === null) {
    return <Spinner animation="border" role="status" size="sm">
      <span className="visually-hidden">Loading events...</span>
    </Spinner>;
  }

  return (
    <Card>
      <Card.Header>üìÖ Upcoming Events</Card.Header>
      <ListGroup variant="flush">
        {events.length === 0 && (
          <ListGroup.Item className="text-muted">No upcoming events.</ListGroup.Item>
        )}
        {events.map(evt => (
          <ListGroup.Item
            action
            href={evt.url}
            target="_blank"
            key={evt.uid}
          >
            <div className="fw-bold text-truncate">{evt.summary}</div>
            <small className="text-muted">
              {new Date(evt.start).toLocaleString('en-GB', {
                weekday: 'short',
                day: 'numeric',
                month: 'short',
                hour: '2-digit',
                minute: '2-digit',
              })}
              {evt.location && ` ‚Ä¢ ${evt.location}`}
            </small>
          </ListGroup.Item>
        ))}
      </ListGroup>
    </Card>
  );
}
